import React, { useEffect, useMemo } from "react";
import { CssBaseline } from "@mui/material";

//react router
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";

//components
import Header from "./components/Header";
import Footer from "./components/Footer";

//pages
import Home from "./screens/HomePage";
import About from "./screens/AboutPage";
import Blog from "./screens/BlogPage";
import Portfolio from "./screens/PortfolioPage";
import Contact from "./screens/ContactPage";
import Error from "./screens/ErrorPage";

import Auth from "./screens/Auth/Auth";

import Profile from "./screens/Auth/User/Profile";

import Dashboard from "./screens/Auth/Admin/DashboardPage";

import Recents from "./screens/Auth/Admin/RecentsPage";
import Posts from "./screens/Auth/Admin/PostsPage";
import ContactList from "./screens/Auth/Admin/ContactListPage";
import Users from "./screens/Auth/Admin/UsersPage";
import ProfileAdmin from "./screens/Auth/Admin/ProfilePage";

import { useDispatch, useSelector } from "react-redux";
import { getPosts } from "./redux/actions/post";

const App = () => {
  const dispatch = useDispatch();
  const posts = useSelector((state) => state.post);

  const computeCategories = (allPost) => {
    const categories = [
      { name: "Skills", posts: [] },
      { name: "Experiences", posts: [] },
      { name: "Education", posts: [] },
      { name: "Services", posts: [] },
      { name: "Portfolio", posts: [] },
      { name: "Clients", posts: [] },
      { name: "Blogs", posts: [] },
      { name: "Work Process", posts: [] },
    ];

    allPost.forEach((post) => {
      switch (post.categoryName) {
        case "Skills":
          if (!categories[0].posts.includes(post)) {
            return categories[0].posts.push(post);
          }
          break;

        case "Experiences":
          if (!categories[1].posts.includes(post)) {
            return categories[1].posts.push(post);
          }
          break;

        case "Education":
          if (!categories[2].posts.includes(post)) {
            return categories[2].posts.push(post);
          }
          break;

        case "Services":
          if (!categories[3].posts.includes(post)) {
            return categories[3].posts.push(post);
          }
          break;

        case "Portfolio":
          if (!categories[4].posts.includes(post)) {
            return categories[4].posts.push(post);
          }
          break;

        case "Clients":
          if (!categories[5].posts.includes(post)) {
            return categories[5].posts.push(post);
          }
          break;

        case "Blogs":
          if (!categories[6].posts.includes(post)) {
            return categories[6].posts.push(post);
          }
          break;

        case "Work Process":
          if (!categories[7].posts.includes(post)) {
            return categories[7].posts.push(post);
          }
          break;
        default:
          break;
      }
    });
    console.log(categories);
    return categories;
  };

  const getCategories = useMemo(() => {
    return computeCategories(posts);
  }, [posts]);

  useEffect(() => {
    dispatch(getPosts());
  }, [dispatch]);
  return (
    <Router>
      <CssBaseline />
      <Header />
      <Routes>
        <Route
          exact
          path="/"
          element={<Home posts={posts} getCategories={getCategories} />}
        />
        <Route
          exact
          path="/about"
          element={
            <About
              posts={posts}
              getSeven={getCategories[7]}
              getFive={getCategories[5]}
            />
          }
        />
        <Route
          exact
          path="/blog"
          element={
            <Blog
              posts={posts}
              getSix={getCategories[6]}
              getFive={getCategories[5]}
            />
          }
        />
        <Route
          exact
          path="/portfolio"
          element={
            <Portfolio
              posts={posts}
              getFour={getCategories[4]}
              getFive={getCategories[5]}
            />
          }
        />
        <Route exact path="/contact" element={<Contact />} />
        <Route exact path="/auth/signin" element={<Auth />} />
        <Route exact path="/auth/signup" element={<Auth />} />
        <Route exact path="/profile/:id" element={<Profile />} />
        <Route exact path="/dashboard" element={<Dashboard />} />
        <Route exact path="/dashboard/recents" element={<Recents />} />
        <Route exact path="/dashboard/posts" element={<Posts />} />
        <Route exact path="/dashboard/contacts" element={<ContactList />} />
        <Route exact path="/dashboard/users" element={<Users />} />
        <Route exact path="/dashboard/profile" element={<ProfileAdmin />} />
        <Route path="*" element={<Error />} />
      </Routes>
      <Footer />
    </Router>
  );
};

export default App;
