import React from "react";

import { Typography, Container } from "@mui/material";

//images
import leafYellow from "../../images/illustrations/leaf-yellow.png";
import leafOrange from "../../images/illustrations/leaf-orange.png";
import leafPinkRound from "../../images/illustrations/leaf-pink-round.png";
import leafCyan from "../../images/illustrations/leaf-cyan-2.png";
import leafBg from "../../images/illustrations/leaf-bg.png";
import pageTitle from "../../images/illustrations/page-title.png";
/*import dotOrange from "../../images/illustrations/dots-orange.png";
import dotGroupOrange from "../../images/illustrations/dots-group-orange.png"; */

import {
  FacebookOutlined,
  Instagram,
  LinkedIn,
  GitHub,
} from "@mui/icons-material";

const Banner = ({ colors, full, bannerName, centered }) => {
  return (
    <>
      <div
        className="banner"
        style={
          !full
            ? {
                backgroundColor: colors.purple,
                paddingTop: "150px",
                backgroundImage: `url(${leafBg})`,
                backgroundPosition: "relative",
                backgroundPositionY: "-300px",
              }
            : {
                backgroundImage: `url(${pageTitle})`,
                backgroundColor: colors.purple,
                backgroundPosition: "relative",
                backgroundPositionY: "90%",
                backgroundPositionX: "50%",
                backgroundRepeat: "no-repeat",
                paddingTop: "50px",
                paddingBottom: "170px",
              }
        }
      >
        <Container maxWidth="lg" align="center">
          {!full ? (
            <>
              <Typography
                align="left"
                fontFamily="Poppins"
                color={colors.white}
                variant="h2"
              >
                I'm Joel Yvan
              </Typography>
              <Typography
                align="left"
                color={colors.white}
                fontFamily="Poppins"
                variant="h2"
                gutterBottom
              >
                Web and Mobile Developer
              </Typography>
              <img src={leafYellow} alt="images" width="100px" />
              <img src={leafOrange} alt="images" width="100px" />
              <img src={leafCyan} alt="images" width="100px" />
              <img src={leafPinkRound} alt="images" width="100px" />
            </>
          ) : (
            <Typography
              align={centered ? "center" : "left"}
              paddingTop={15}
              style={full ? { position: "relative", top: "100px" } : {}}
              color={colors.white}
              fontFamily="Poppins"
              fontWeight="bold"
              variant="h2"
            >
              {bannerName}
            </Typography>
          )}
        </Container>

        {!full && (
          <div
            style={{
              flexDirection: "column",
              paddingLeft: "15px",
              display: "flex",
              paddingBottom: "20px",
            }}
          >
            <a
              href="https://web.facebook.com/gaucho.neymar.1"
              rel="noreferrer"
              target="_blank"
            >
              <FacebookOutlined style={{ color: colors.white }} />
            </a>

            <a
              href="https://www.instagram.com/mattcorradi/"
              rel="noreferrer"
              target="_blank"
            >
              <Instagram style={{ color: colors.white, paddingTop: "5" }} />
            </a>

            <a
              href="https://www.linkedin.com/in/joel-noumbi/"
              rel="noreferrer"
              target="_blank"
            >
              <LinkedIn style={{ color: colors.white, paddingTop: "5" }} />
            </a>

            <a
              href="https://github.com/Noumbi-joel "
              rel="noreferrer"
              target="_blank"
            >
              <GitHub style={{ color: colors.white, paddingTop: "5" }} />
            </a>
          </div>
        )}
      </div>

      {!full && (
        <Container maxWidth="sm">
          <Typography
            variant="h5"
            style={{
              fontSize: "22px",
              marginTop: "20px",
              fontFamily: "Poppins",
              textAlign: "justify",
            }}
            paragraph
            align="center"
            color={colors.black}
          >
            Since beginning my journey as a web and mobile developer nearly 02
            years ago, I've done remote work for one agency, consulted for
            startups, and collaborated with talented people to create digital
            products for both business and consumer use. I'm quietly confident,
            naturally curious, and perpetually working on improving my chops one
            coding problem at a time.
          </Typography>

          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <a
              href="https://www.linkedin.com/in/joel-noumbi/"
              target="_blank"
              rel="noreferrer"
              style={{
                fontFamily: "Poppins",
                fontSize: "22px",
                fontWeight: "bold",
                textDecoration: "none",
                color: colors.purple,
              }}
            >
              Know More
            </a>
          </div>
        </Container>
      )}
    </>
  );
};

export default Banner;
