import React from "react";

import client1 from "../images/testimonials/client.jpg";

import colors from "../utils/Colors";

import bgDots from "../images/illustrations/bg-dots.png";

//components
import Slider from "../components/Slider";
import Categories from "../components/Categories";
import Education from "../components/Education";
import Portfolio from "../components/Portfolio";
import Banner from "../components/Banner";
import Testimony from "../components/Testimony";

import { CircularProgress, Grid, Typography } from "@mui/material";
import { Box } from "@mui/system";

const testimonies = [
  {
    msg: "Me Nakam Djibril I propose it to you without any other form of procedure this young is calm and talented. In my humble opinion I believe that he can do great things in the future.",
    clientImg: client1,
    clientName: "Djibril Nakam Benjamin",
    clientRes: "CEO, Agogolines",
  },
];

function HomePage({ posts, getCategories }) {
  return (
    <>
      <Banner colors={colors} bgImg={"leafYellow"} />

      {posts.length <= 0 && (
        <Box textAlign="center">
          <CircularProgress />
          <Typography variant="body1" fontFamily="Poppins">
            Wait a moment please...
          </Typography>
        </Box>
      )}

      {getCategories && (
        <>
          <Categories
            type={[getCategories[0].name, getCategories[0].posts]}
            nolist={false}
            exp={false}
          />

          <Categories
            type={[getCategories[1].name, getCategories[1].posts]}
            nolist={false}
            exp={true}
          />

          <Education
            educations={[getCategories[2].name, getCategories[2].posts]}
            bgDots={bgDots}
            colors={colors}
          />

          <Categories
            type={[getCategories[3].name, getCategories[3].posts]}
            nolist={false}
            exp={true}
            sd={true}
          />

          <Portfolio
            noGroupBtn={false}
            type={[getCategories[4].name, getCategories[4].posts]}
            color={colors}
          />

          <Testimony testimonies={testimonies} colors={colors} />

          <Grid
            container
            spacing={2}
            style={{
              display: "flex",
              justifyContent: "center",
              marginTop: "50px",
            }}
          >
            <Slider clientsList={[getCategories[5].posts]} />
          </Grid>

          <Categories
            type={[getCategories[6].name, getCategories[6].posts]}
            exp={true}
            nolist={false}
            actions
            sd={true}
          />
        </>
      )}
    </>
  );
}

export default HomePage;
