import React from "react";

//colors
import colors from "../utils/Colors";

import signature from "../images/about/signature.png";

//components
import Slider from "../components/Slider";
import Banner from "../components/Banner";
import Categories from "../components/Categories";

//mui
import { Box, CircularProgress, Grid, Typography } from "@mui/material";

//images
import author from "../images/blogs/lijay.jpg";

/* import { useDispatch, useSelector } from "react-redux";

import { getPosts } from "../redux/actions/post"; */

function About({ posts, getSeven, getFive }) {
  return (
    <div>
      <Banner bannerName="About Me" colors={colors} full />
      <Grid align="center" pt={3}>
        <Grid container direction="row" spacing={20} maxWidth="lg">
          <Grid item xs={12} sm={6}>
            <Box pl={5} pr={5}>
              <Typography
                variant="h6"
                paragraph
                align="justify"
                style={{
                  fontWeight: "400",
                  fontSize: "15px",
                  fontFamily: "Poppins",
                }}
              >
                Noumbi Joel Yvan is a modern web and mobile developer, with 02
                years experiences managing digital business by joining a team or
                by himself. His background in software engineering, coding, and
                life inform his mindful but competitive approach. Joel Yvan is
                fueled by his passion for understanding the nuances of
                web/mobile business. He considers himself a ‘forever student,’
                eager to both build on his academic foundations in computer
                science and software engineering and stay in tune with the
                latest digital strategies through continued coursework.
              </Typography>
              <Typography
                variant="h6"
                paragraph
                align="justify"
                style={{
                  fontWeight: "400",
                  fontSize: "15px",
                  fontFamily: "Poppins",
                }}
              >
                His hunger for knowledge and determination to turn information
                into action has contributed to his most recent success at
                Agogolines Group, where he helped to build a powerful carpooling
                app. Meanwhile, he vastly improved the productivity of his team
                by implementing strategic project management methods and
                ensuring a work-life balance for his department.
              </Typography>
              {/* <Typography
                variant="h6"
                paragraph
                align="justify"
                style={{
                  fontWeight: "400",
                  fontSize: "15px",
                  fontFamily: "Poppins",
                }}
              >
                Madison believes mindfulness in the workplace is key to success
                - a tenet she lives out through her interests in yoga,
                meditation, gardening, and painting. Madison is currently
                working as a freelance marketing director and is always
                interested in a challenge. Reach out to
                madisonblackstone@gmail.com to connect!
              </Typography> */}
            </Box>
          </Grid>
          <Grid item xs={12} sm={6}>
            <Grid container direction="column">
              <Box>
                <img src={author} alt="author" width="280px" height="400px" />
                <img
                  src={signature}
                  alt="author"
                  width="200px"
                  height="100px"
                />
                <Typography
                  variant="h5"
                  fontWeight="bold"
                  fontFamily="Poppins"
                  fontSize="22px"
                >
                  Noumbi Joel Yvan
                </Typography>
              </Box>
            </Grid>
          </Grid>
        </Grid>
      </Grid>

      {posts.length <= 0 && (
        <Box textAlign="center">
          <CircularProgress />
          <Typography variant="body1" fontFamily="Poppins">
            Wait a moment please...
          </Typography>
        </Box>
      )}

      {getFive && (
        <>
          <Categories
            type={[getSeven.name, getSeven.posts]}
            exp={true}
            sd={true}
          />
          <Categories type={["My Clients"]} exp={true} nolist={true} />
          <Grid
            container
            spacing={2}
            style={{
              display: "flex",
              justifyContent: "center",
              marginTop: "50px",
            }}
          >
            <Slider clientsList={getFive.posts} />
          </Grid>
        </>
      )}
    </div>
  );
}

export default About;
